}


.article-filter {
	height: 50px;
	background-color: #009688;
	border: none;
	box-shadow: none;
}

.artcile-filter_header {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	
	
}
.article-filter .collapsible-header {
	padding: 2px!important;
}

.artcile-filter_header button {
	color: #fff;
	margin-left: 20px;
}

.article-filter .collapsible-body {
	position: relative;
	z-index: 1000;
	border-bottom: 1px solid #ddd;
}

.artcile-filter_header-title {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.article-filter * {
	background-color: #009688;
}

.article-filter_title p {
	margin-left: 15px;
}
.article-filter_title {
	display: flex;
	align-items: center;
}

.article-filter_chip {
	font-weight: bold;
	cursor: pointer;
	border: 1px #fff solid;
	color: #fff!important;
	background-color: transparent!important;
	transition: 0.2s;
}

.article-filter_chip:hover {
	color:#282c34a2!important;
	background-color: #fff!important;
}

.article-filter_chip[selected] {
	font-weight: bold;
	color:#282c34a2!important;
	background-color: #fff!important;
}

.article-filter_chip[disabled] {
	font-weight: bold;
	color: rgba(255, 255, 255, 0.5)!important;
	border: 1px rgba(255, 255, 255, 0.5) solid;
	background-color: transparent!important;
}

.admin_mediainsp {
	position: relative;
}
.admin_mediainsp::before {
	position: absolute;
	display: block;
	color: #fff;
	line-height: 10em;
	width: 250px;
	height: 150px;
	content: attr(data-name);
	text-align: center;
	font-size: 16px;
	background-color: #000;
	opacity: 0;
	cursor: pointer;
	transition: .2s;
}
.admin_mediainsp:hover::before {
	opacity: 0.7;
}

