}

.ArticleRPageWrapper {
	display: flex;
}

.ArticleRPage-header .article-info {
	margin-left: auto;
	margin-right: auto;
	margin-top: -25px;
	display: flex;

}

.article-inbody {
	padding-left: 25px;
}

.ArticleRPage-header .article-info span {
	margin-right: 10px;
}

.ArticleRPage-header {
	display: flexbox;
