}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

@media (max-width: 1170px) {
	.cards-wrapper .article-card:nth-child(3) {
		margin-top: 20px;
	}
}

@media (max-width: 957px) {

	.carousel-item .article-text {
		width: 100%;
		height: 40%;
	}
}

@media (max-width: 760px) {
	.cards-wrapper .article-card:nth-child(2) {
		margin-top: 15px;
	}
	.cards-wrapper .article-card {
		margin-top: 15px;
	}
	.article-filter_chip:hover {
		color:#fff!important;
		background-color: transparent!important;
	}
	.articles-breadcrumb {
		width: 80%;
	}
	.article-text {
		width: 100%;
		font-size: 16px;
		text-align: left;
	}
	.ArticlesPage-footer {
		display: flex;
		flex-direction: column;
		justify-items: start;
		justify-content: start;
	}
	.searched-info {
		justify-content: space-around;
	}
	.cards-wrapper {
		margin-bottom: 0;
	}
}

@media (max-width: 600px) {
	main {
		padding-top: 58px;
	}

	.brand-logo__img {
		width: 57px;
		height: 50px;
	}
}

@media (max-width: 575px) {
	.container {
		padding-left: 25px;
		padding-right: 25px;
	}
	.article-inbody {
		padding-left: 0px;
	}
	.carousel-heading {
		font-size: 26px;
	}
	.articles-breadcrumb {
		margin: 0;
		text-align: left;
		width: fit-content;
		height: 40px;
	}
	.article-content-list_wrapper {
		width: 100%
	}
	.back-button {
		display: none;
	}

	.article-body {
		padding-left: 20px;
	}
	.ArticlesPage-header {
		flex-direction: column;
	}
}
@media (max-width: 473px) {
	.carousel-item .article-text {
		height: 50%;
	}
	.carousel-heading {
		font-size: 26px;
		margin-bottom: 20px;
	}
	/* .breadcrumb {
		font-size: 16px !important;
		overflow: hidden;
	} */
	.searched-info {
		flex-direction: column;
		
	}
}
@media (max-width: 415px) {
	.breadcrumb {
		font-size: 14px !important;
	}
	.searched-item {
		font-size: 14px;
	}
	.article-heading {
		font-size: 32px;
	}
	#article-content-list {
		font-size: 16px;
	}
	.article-subtitle {
		font-size: 20px;
	}
	.pagination-select input {
		font-size: 14px!important;
		height: 26px!important;
		width: 46px!important;
	}
}
